.cpager {
    display: flex;
    flex-direction: column;
    gap: var(--space-sm);
}

.cpager .cpager-track {
    display: flex;
    gap: var(--space);
    overflow-x: auto;
    overflow-y: visible;
    scroll-snap-type: x mandatory;
    padding-bottom: var(--space-xs);
    scrollbar-width: none;
    -ms-overflow-style: none;
}

.cpager .cpager-track::-webkit-scrollbar {
    display: none;
}



.cpager .cpager-track {
    padding-block: var(--space-xs);
}

.cpager-col:first-child {
    margin-left: var(--space-xs);

}

.cpager-col:last-child {}

.cpager-col {
    flex: 0 0 calc((100% - 2 * var(--space)) / 3);
    display: grid;
    grid-template-rows: 1fr 1fr;
    gap: var(--space);
    scroll-snap-align: start;
}

.cpager-col:first-child {
    margin-left: var(--space-xs);
}

.cpager-col:last-child {
    margin-right: var(--space-xs);
}

/* Mobile tuning: larger tiles and tighter gaps, while keeping 3x2 columns */
@media (max-width: 560px) {
  .cpager .cpager-track {
    gap: var(--space-sm);
  }
  .cpager-col {
    flex: 0 0 calc((100% - 2 * var(--space-sm)) / 3);
    gap: var(--space-sm);
  }
}

.cpager-dots {
    display: flex;
    justify-content: center;
    gap: var(--space-xs);
}

.cpager-dot {
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background: #d0c4cb;
    border: none;
    padding: 0;
    display: inline-block;
}

.cpager-dot.active {
    background: var(--ink);
    transform: scale(1.1);
}
